@media only screen and (min-width:320px) and (max-width:768px) {
  .minhead {
    display: block;
  }

  .head {
    display: none;
  }

  .canvasText {
    display: none;
  }

  .minTeaForYou {
    display: block!important;
  }

  .content .teaForYou {
    height: 25rem;
  }

  .content .teaForYou .contactLogo i {
    font-size: 1.875rem;
  }

  .content .AboutMe .textTitle {
    font-size: 1.875rem;
  }

  .content .AboutMe p {
    margin: 0;
    font-size: 0.8125rem;
  }

  .content .AboutMe section {
    width: 90%;
    padding: 1.25rem;
    background: #fff;
    border-radius: 0.3125rem;
    margin-left: 5%;
    margin-bottom: 1.25rem;
  }

  .content .AboutMe section .img {
    border: none;
    width: 5rem;
    height: 5rem;
    box-shadow: none;
    margin-bottom: 0;
  }

  .content .AboutMe .information .img {
    background: url(./images/idnetity.gif);
    background-size: cover;
  }

  .content .AboutMe .job .img {
    background: url(./images/work.gif);
    background-size: cover;
  }

  .content .AboutMe .dream .img {
    background: url(./images/dream.gif);
    background-size: cover;
  }

  .content .myGoal {
    height: 18.75rem;
  }

  .content .myGoal p:first-child {
    line-height: 3.125rem;
    font-size: 1.25rem;
  }

  .content .myGoal p:last-child {
    font-size: 0.9375rem;
  }

  .content .travelGoal h3 {
    font-size: 1.25rem;
  }

  .content .travelGoal h4 {
    font-size: 0.8125rem;
  }

  .content .travelGoal .des p {
    font-size: 0.75rem;
  }
}

