/*支持less、sass,需引入相应的包，详细请看http://blog.csdn.net/itkingone/article/details/74729508*/

body {
  background: url('images/bg.jpg') center center;
  background-size: cover;
  width: 100%;
  font-family: Open Sans, Tahoma, Arial, Roboto, Droid Sans, Helvetica Neue, Droid Sans Fallback, Heiti SC, Hiragino Sans GB, Simsun, sans-self;
}

#bgvid {
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  -ms-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.qinghua {
  position: absolute;
  width: 100%;
  height: 12.5rem;
  background: red;
  font-size: 1rem;
  left: 0;
  right: 0;
  top: 9.375rem;
  overflow: hidden;
  padding: 0.625rem;
  color: #fff;
}

.qinghua .list {
  position: absolute;
  top: 6.25rem;
  width: 100%;
}

.head,
.minhead {
  width: 100%;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  position: fixed;
  height: 3.75rem;
  line-height: 3.75rem;
  left: 0;
  transition: all 0.5s;
  z-index: 100;
}

.head i,
.minhead i {
  font-size: 1.5rem;
  margin-right: 0.625rem;
  vertical-align: middle;
}

.head:hover {
  background: rgba(0, 0, 0, 0.9);
}

.head_main {
  width: 80%;
  margin: 0 auto;
  height: 3.75rem;
  line-height: 3.75rem;
}

.minhead {
  display: none;
  background: rgba(0, 0, 0, 0.8);
  box-shadow: 0.0625rem 0.0625rem 0.0625rem #ededed;
}

.minhead .head_main {
  font-size: 1.125rem;
  text-align: center;
  width: 90%;
}

.nav li {
  float: left;
  list-style: none;
  padding: 0 1.25rem;
  font-size: 1rem;
}

.nav li a {
  color: #fff;
}

.nav li i {
  font-size: 1.125rem;
  margin-right: 0.375rem;
}

.heroRank a {
  color: #fff;
}

.subNav {
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
}

.subNav li {
  text-align: center;
  border-bottom: solid 0.0625rem #666;
}

.subNav a {
  color: #fff;
  display: block;
}

.subNav a:active {
  background: #666;
}

.content {
  position: relative;
  top: 3.75rem;
  left: 0;
  width: 100%;
  text-align: center;
}

.content .teaForYou {
  background: rgba(0, 0, 0, 0.5);
  height: 37.5rem;
}

.content .teaForYou .canvasText #DrawText {
  width: 43.75rem;
  height: 25rem;
  opacity: 1;
  animation: showText ease 2s;
  -webkit-animation: showText ease 2s;
}

.content .teaForYou .showWX {
  display: inline-block;
  margin-left: 13%;
  width: 8%;
}

.content .teaForYou .showWX img {
  width: 100%;
}

.content .teaForYou .minTeaForYou {
  color: #fff;
  font-size: 1.875rem;
  display: none;
  width: 100%;
  height: 17.5rem;
  line-height: 17.5rem;
}

.content .teaForYou .contactLogo {
  margin: 0 auto;
  width: 50%;
  position: relative;
  overflow: hidden;
  height: 11%;
}

.content .teaForYou .contactLogo a {
  display: inline-block;
  width: 25%;
  float: left;
  position: absolute;
}

.content .teaForYou .contactLogo .github {
  right: 75%;
  animation: showLogoGithub ease .8s;
  -webkit-animation: showLogoGithub ease .8s;
}

.content .teaForYou .contactLogo .blog {
  right: 50%;
  animation: showLogoBlog ease 1s;
  -webkit-animation: showLogoBlog ease 1s;
}

.content .teaForYou .contactLogo .weixin {
  right: 25%;
  animation: showLogoWeixin ease 1.2s;
  -webkit-animation: showLogoWeixin ease 1.2s;
}

.content .teaForYou .contactLogo .qq {
  right: 0%;
  animation: showLogoQQ ease 1.4s;
  -webkit-animation: showLogoQQ ease 1.4s;
}

.content .teaForYou .contactLogo i {
  color: #fff;
  font-size: 3.5625rem;
  display: inline-block;
}

.content .AboutMe {
  background: rgba(255, 255, 255, 0.65);
  width: 100%;
}

.content .AboutMe .textTitle {
  widows: 100%;
  text-align: center;
  font-size: 2.8125rem;
  line-height: 6.25rem;
  color: #444;
}

.content .AboutMe p {
  text-align: left;
}

.content .AboutMe section {
  float: left;
  width: 33.3333%;
  padding: 2%;
  line-height: 1.6em;
}

.content .AboutMe section .img {
  margin: 0 auto 1.875rem;
  text-align: center;
  border-radius: 6.25rem;
  width: 7.5rem;
  height: 7.5rem;
  border: 1.25rem solid #fff;
  box-shadow: 0 0 0.0625rem #333;
}

.content .AboutMe .inforDetail {
  max-width: 75rem;
  margin: 0 auto;
  overflow: hidden;
}

.content .AboutMe .information .img {
  background: url(images/idnetity.jpg);
  background-size: cover;
}

.content .AboutMe .information:hover .img {
  background: url(images/idnetity.gif);
  background-size: cover;
}

.content .AboutMe .job .img {
  background: url(images/work.jpg);
  background-size: cover;
}

.content .AboutMe .job:hover .img {
  background: url(images/work.gif);
  background-size: cover;
}

.content .AboutMe .dream .img {
  background: url(images/dream.jpg);
  background-size: cover;
}

.content .AboutMe .dream:hover .img {
  background: url(images/dream.gif);
  background-size: cover;
}

.content .myGoal {
  background: url(images/mitor.jpg);
  background-size: cover;
  width: 100%;
  height: 50rem;
}

.content .myGoal p:first-child {
  margin: 0;
  color: #fff;
  line-height: 9.375rem;
  font-size: 2.8125rem;
}

.content .myGoal p:last-child {
  margin: 0;
  color: #fff;
  font-size: 1.5625rem;
}

.content .travelGoal {
  background: rgba(255, 255, 255, 0.65);
  width: 100%;
  padding: 2% 0;
}

.content .travelGoal h3 {
  color: #333;
  font-size: 2.1875rem;
  font-weight: 300;
}

.content .travelGoal h4 {
  color: #666;
  font-size: 1.5625rem;
  font-weight: 300;
}

.content .travelGoal .des {
  width: 80%;
  margin: 0 auto;
}

.content .travelGoal .des p {
  color: #333;
  font-size: 1.25rem;
  text-align: left;
}

.contactMe {
  background: #363941;
  overflow: hidden;
  color: #ccc;
  position: relative;
}

.contactMe p {
  text-align: left;
}

.contactMe h3 {
  font-size: 4.125rem;
  font-weight: 300;
  margin: 2.5rem 0 0.9375rem;
  text-align: center;
}

.contactMe h4 {
  font-size: 1.625rem;
  font-weight: 300;
  text-align: center;
}

.contactMe .btn {
  padding: 0.5rem 0;
  width: 15rem;
  display: inline-block;
  background: #484b53;
  line-height: 2.5rem;
}

.contactMe .btn:hover {
  background: #000;
  cursor: pointer;
}

.contactMe input,
.contactMe textarea {
  color: #ccc;
  border: none;
  background: #484b53;
  padding: 0.625rem;
  font-size: 0.875rem;
  box-sizing: border-box;
  width: 90%;
}

.contactMe input:disabled,
.contactMe textarea:disabled {
  color: #999;
}

.contactMe textarea {
  width: 95%;
  height: 8em;
}

.index-content-tips {
  margin-left: 0.625rem;
  color: #A5A5A5;
  opacity: 0;
  transition: all 0.8s ease-out;
}

.index-content-box {
  margin: 2.5rem auto;
  padding: 2.5rem 0 2.5rem 3.5%;
  max-width: 75rem;
  overflow: hidden;
  border-top: 0.0625rem solid #484b53;
}

.index-content-boxblock {
  overflow: hidden;
}

.index-content-message {
  box-sizing: border-box;
  padding: 0.3125rem;
}

.index-content-boxhalf {
  float: left;
  width: 50%;
}

/*移动端菜单动画*/

.slide-fade-enter-active {
  transition: all .3s ease;
}

.slide-fade-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}

.slide-fade-enter,
.slide-fade-leave-active {
  -ms-transform: translateX(0.625rem);
  transform: translateX(0.625rem);
  opacity: 0;
}

@keyframes showText {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes showLogoGithub {
  0% {
    right: -100%;
  }

  100% {
    right: 75%;
  }
}

@keyframes showLogoBlog {
  0% {
    right: -100%;
  }

  100% {
    right: 50%;
  }
}

@keyframes showLogoWeixin {
  0% {
    right: -100%;
  }

  100% {
    right: 25%;
  }
}

@keyframes showLogoQQ {
  0% {
    right: -100%;
  }

  100% {
    right: 0%;
  }
}


